import { useState, useEffect } from "react";
import { useQueryClient } from "@tanstack/react-query";
import { axiosDelete, axiosGet } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { NothingFound } from "@/assets/IntegrationIcons";
import Skeleton from "react-loading-skeleton";
import useStore from "@/pages/omni-builder/store";

interface Props {
  onSuccess?: (config: Record<string, any>) => void;
  onClose: () => void;
}

export default function Gmail({ onSuccess, onClose }: Props) {
  const queryClient = useQueryClient();
  const [accounts, setAccounts] = useState<any>([]);
  const [loader, setLoader] = useState<boolean>(true);
  const [mainLoader, setMainLoader] = useState<boolean>(true);
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const [selectedConfig, setSelectedConfig] = useState<Record<
    string,
    any
  > | null>(null);
  const atAgent: boolean =
    window.location.pathname.includes("agent") ||
    window.location.pathname.includes("onboarding");
  const dispatch = useDispatch();

  useEffect(() => {
    getAccounts();
  }, []);

  const getAuthorizationUrl = () => {
    setMainLoader(true);
    axiosGet("/configurations/authLink/gmail")
      .then((response) => {
        const authWindow = window.open(
          response?.data.authUrl,
          "_blank",
          "width=600,height=600",
        );
        // onClose();
        const checkClosed = setInterval(() => {
          if (authWindow?.closed) {
            axiosGet(`/configurations/emailConfigs?id=${true}&esp=gmail`)
              .then((response: any) => {
                if (response.data.length > 0) {
                  dispatch(
                    userSlice.actions.setToastPopUp({
                      show: true,
                      toastType: "success",
                      message: "Configuration added successfully",
                    }),
                  );
                  // onSuccess?.(response.data[0]);
                  setSelectedConfig(response.data[0]);
                  setMainLoader(false);
                  setAccounts((prev) => [...prev, response.data[0]]);
                  queryClient.invalidateQueries({
                    queryKey: ["configurations", "email"],
                  });
                }
              })
              .catch((err) => {
                dispatch(
                  userSlice.actions.setToastPopUp({
                    show: true,
                    toastType: "error",
                    message:
                      err.response?.data?.message ??
                      err.message ??
                      "Something went wrong",
                  }),
                );
                setLoader(false);
              });
            clearInterval(checkClosed);
            dispatch(userSlice.actions.setIntegration(true));
            setMainLoader(false);
          }
        }, 1000);
      })
      .catch((err) => {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message:
              err.response?.data?.message ??
              err.message ??
              "Something went wrong",
          }),
        );
        setMainLoader(false);
      });
  };

  const getAccounts = () => {
    setLoader(true);
    axiosGet(`/configurations/emailConfigs?id=${false}&esp=gmail`)
      .then((response: any) => {
        // let data= JSON.parse(response)
        if (response.data.length > 0) {
          setMainLoader(false);
          setAccounts(response.data);
        } else {
          getAuthorizationUrl();
        }
        setLoader(false);
      })
      .catch((err) => {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message:
              err.response?.data?.message ??
              err.message ??
              "Something went wrong",
          }),
        );
        setLoader(false);
      });
  };

  const handleLogout = async (id: any) => {
    setLoader(true);

    await axiosDelete(`/configurations/oAuthEmailConfig?configId=${id}`)
      .then((response) => {
        let index = accounts.findIndex((element: any, index) => {
          if (element._id === id) {
            return true;
          }
        });
        accounts.splice(index, 1);
        setAccounts(accounts);
        dispatch(userSlice.actions.setIntegration(false));
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: "Configuration deleted successfully!",
          }),
        );
        queryClient.invalidateQueries({
          queryKey: ["configurations", "email"],
        });
        console.log(response?.data?.message, "error");
        setLoader(false);
      })
      .catch((err) => {
        setLoader(false);
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message: `${
              err.response?.data.message
                ? err.response.data.message
                : err.message
            }`,
          }),
        );
      });
  };

  if (mainLoader) return null;
  return (
    <>
      <div className="general-container">
        <div
          className="overflow"
          onClick={() => {
            onClose();
          }}
        >
          <div
            className="no-data-add-button integration-content"
            onClick={(e) => {
              e.stopPropagation();
            }}
          >
            {/* MODAL HEADER */}
            <div className="modal-head">
              <h2>
                {loader ? (
                  <Skeleton height={15} width={250} />
                ) : (
                  "Send Omniengage Data To Gmail"
                )}
              </h2>
              {loader ? (
                <Skeleton circle={true} height={30} width={30} />
              ) : (
                <img src="https://cdn.filestackcontent.com/2KbGnloKSLOOAUoJ7LoA" />
              )}

              <div className="closing-modal" onClick={onClose}>
                <button>
                  <svg
                    width="10"
                    height="10"
                    viewBox="0 0 10 10"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                      fill="#6D6D6D"
                    />
                  </svg>
                </button>
              </div>
            </div>
            <div className="fb-wrap">
              <div className="facebook-modal-content fx jstfy-sp-btw">
                <div className="skeletonPages">
                  <h1>
                    {loader ? (
                      <Skeleton height={10} width={305} />
                    ) : (
                      "Connect to Gmail"
                    )}{" "}
                  </h1>
                  <h2>
                    {loader ? (
                      <Skeleton height={10} width={305} />
                    ) : (
                      "Connect your Gmail account with our workflow"
                    )}{" "}
                  </h2>
                </div>
              </div>
              <div className="modal-content-table new-scrollbar skeletonPages">
                {/* <h3>
                  {" "}
                  {loader ? (
                    <Skeleton height={17} width={195} />
                  ) : accounts?.length == 0 ? (
                    "Your Gmail Accounts"
                  ) : (
                    !search && "No Gmail Accounts Available"
                  )}{" "}
                </h3> */}
                {loader ? (
                  <>
                    <li className={"react-loading-skeleton"}> </li>
                    <li className={"react-loading-skeleton"}> </li>
                  </>
                ) : accounts?.length > 0 ? (
                  <ul className="modal-content-list">
                    {accounts?.map((data, index) => (
                      <li key={index} style={{ display: "flex" }}>
                        <div className="list-left gap-10">
                          {atAgent && (
                            <label className="assign-logic fx cp int_radio">
                              <input
                                type="radio"
                                onClick={() => {
                                  setSelectedConfig(data);
                                }}
                                checked={selectedConfig?._id === data._id}
                              />
                            </label>
                          )}
                          <label className="label-modal">{data?.sender}</label>
                        </div>
                        <div className="list-right">
                          <div
                            className="del-int-modal"
                            onClick={() => handleLogout(data?._id)}
                          >
                            <svg
                              width={14}
                              height={16}
                              viewBox="0 0 14 16"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M13.3322 2.66667H11.2656C11.1108 1.91428 10.7014 1.23823 10.1064 0.75248C9.51134 0.266727 8.76702 0.000969685 7.99888 0L6.66554 0C5.8974 0.000969685 5.15308 0.266727 4.55804 0.75248C3.96299 1.23823 3.5536 1.91428 3.39887 2.66667H1.3322C1.15538 2.66667 0.985814 2.73691 0.86079 2.86194C0.735765 2.98696 0.665527 3.15653 0.665527 3.33334C0.665527 3.51015 0.735765 3.67972 0.86079 3.80475C0.985814 3.92977 1.15538 4.00001 1.3322 4.00001H1.99886V12.6667C1.99992 13.5504 2.35145 14.3977 2.97635 15.0226C3.60124 15.6474 4.44847 15.999 5.33221 16H9.33222C10.2159 15.999 11.0632 15.6474 11.6881 15.0226C12.313 14.3977 12.6645 13.5504 12.6656 12.6667V4.00001H13.3322C13.509 4.00001 13.6786 3.92977 13.8036 3.80475C13.9287 3.67972 13.9989 3.51015 13.9989 3.33334C13.9989 3.15653 13.9287 2.98696 13.8036 2.86194C13.6786 2.73691 13.509 2.66667 13.3322 2.66667ZM6.66554 1.33334H7.99888C8.4124 1.33384 8.81563 1.46225 9.15329 1.70097C9.49095 1.93968 9.74649 2.277 9.88488 2.66667H4.77954C4.91793 2.277 5.17347 1.93968 5.51113 1.70097C5.84879 1.46225 6.25202 1.33384 6.66554 1.33334ZM11.3322 12.6667C11.3322 13.1971 11.1215 13.7058 10.7464 14.0809C10.3714 14.456 9.86265 14.6667 9.33222 14.6667H5.33221C4.80177 14.6667 4.29306 14.456 3.91799 14.0809C3.54291 13.7058 3.3322 13.1971 3.3322 12.6667V4.00001H11.3322V12.6667Z"
                                fill="#fb5f66"
                              />
                              <path
                                d="M5.9987 12C6.17551 12 6.34508 11.9298 6.4701 11.8048C6.59513 11.6797 6.66537 11.5102 6.66537 11.3334V7.33336C6.66537 7.15654 6.59513 6.98697 6.4701 6.86195C6.34508 6.73693 6.17551 6.66669 5.9987 6.66669C5.82189 6.66669 5.65232 6.73693 5.52729 6.86195C5.40227 6.98697 5.33203 7.15654 5.33203 7.33336V11.3334C5.33203 11.5102 5.40227 11.6797 5.52729 11.8048C5.65232 11.9298 5.82189 12 5.9987 12ZM8.66537 12C8.84218 12 9.01175 11.9298 9.13678 11.8048C9.2618 11.6797 9.33204 11.5102 9.33204 11.3334V7.33336C9.33204 7.15654 9.2618 6.98697 9.13678 6.86195C9.01175 6.73693 8.84218 6.66669 8.66537 6.66669C8.48856 6.66669 8.31899 6.73693 8.19397 6.86195C8.06894 6.98697 7.9987 7.15654 7.9987 7.33336V11.3334C7.9987 11.5102 8.06894 11.6797 8.19397 11.8048C8.31899 11.9298 8.48856 12 8.66537 12Z"
                                fill="#fb5f66"
                              />
                            </svg>
                          </div>
                        </div>
                      </li>
                    ))}
                  </ul>
                ) : (
                  <div className="no-invoice-found">
                    <NothingFound />
                    <h1>Nothing!!</h1>
                    <p>No Gmail Accounts Available</p>
                  </div>
                )}
              </div>
            </div>
            {!loader && (
              <div
                className="new-connections_outer"
                onClick={() => getAuthorizationUrl()}
              >
                <svg
                  width="18"
                  height="18"
                  viewBox="0 0 18 18"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M9 16.5C13.125 16.5 16.5 13.125 16.5 9C16.5 4.875 13.125 1.5 9 1.5C4.875 1.5 1.5 4.875 1.5 9C1.5 13.125 4.875 16.5 9 16.5Z"
                    stroke="#FB5F66"
                    strokeWidth="1.125"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <g>
                    <path
                      d="M6 9H12"
                      stroke="#FB5F66"
                      strokeWidth="1.125"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                    <path
                      d="M9 12V6"
                      stroke="#FB5F66"
                      strokeWidth="1.125"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </g>
                </svg>
                Add New Account
              </div>
            )}

            {atAgent && (
              <div className="modal-footer jstfy-cnt">
                <button
                  className={`save_btn ${!selectedConfig ? "disable" : ""}`}
                  onClick={() => {
                    selectedConfig && onSuccess?.(selectedConfig);
                    setOpenConfigModalId("")
                  }}
                  disabled={!selectedConfig}
                >
                  Continue
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
}
