import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";

export default function useEmailConfig() {
  return useQuery({
    queryKey: ["configurations", "email"],
    queryFn: async () => {
      const response = await axiosGet("/configurations/getAllEmailConfig");
      return response.data.data;
    },
    staleTime: Infinity,
  });
}
